<template>
    <div class="page">
        <xm-form :model="form" :rule="rule" ref="baseForm">
            <xm-form-item label="" prop="name">
                <input v-model="form.name"/>
            </xm-form-item>
            <xm-form-item label="" prop="email">
                <input v-model="form.email"/>
            </xm-form-item>
            <xm-form-item>
                <button @click="submit"></button>
            </xm-form-item>
        </xm-form>
    </div>
</template>

<script>
  import xmForm from "./formItem.vue"
  import xmFormItem from "./xmFormItem.vue"

  export default {
    components: {
      xmForm,
      xmFormItem,
    },
    data() {
      return {
        form: {
          name: "",
          email: ""
        },
        rule: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur'}
          ],
        }
      }
    },
    methods: {
      submit() {
        // formvalidate
        this.$refs.baseForm.validate().then(res => {
          console.log(res)
        }).catch(e => {
          console.log(e)
        })
      }
    }
  }
</script>


<style scoped>

</style>



